
.svg-icon {
  width: 1em;
  height: 1em;
  display: inline-block;
  vertical-align: top;
}
.loading {
  color: #1199FF;
  fill: #004A9E;
}
.loading.monochrome {
  color: #7B7B7B;
  fill: #3D3D3D;
}
.error {
  color: #1199FF;
  fill: #00DCFF;
  stroke: #444466;
}
.error.monochrome {
  color: #B3B3B3;
  fill: #B2B2B2;
  stroke: #666666;
}



