
.placeholder-container {
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: rgba(56, 112, 219, 0.8);
}
.loadFail-container {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
}
.loadFail-container-text {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 14px;
}
.placeholder-img {
  width: 8em;
  height: 6em;
  display: inline-block;
  margin: 0 0.5em 0.5em 0;
  background-position: center center;
  background-repeat: no-repeat;
}
.placeholder-img.error {
  background-image: url(assets/error.svg);
}
.placeholder-img.error.monochrome {
  background-image: url(assets/errorMono.svg);
}
.placeholder-img.loading {
  background-image: url(assets/loading.svg);
  font-size: 0.5em;
}
.placeholder-img.loading.monochrome {
  background-image: url(assets/loadingMono.svg);
}
.placeholder-img.loading.rotation-img {
  color: rgba(0, 0, 0, 0.6);
  width: 1.5em;
  height: 1.5em;
  margin: 0;
  background-image: url(assets/loadIcon.svg);
  -webkit-transform: rotate(360deg);
  animation: rotation 3s linear infinite;
  -moz-animation: rotation 3s linear infinite;
  -webkit-animation: rotation 3s linear infinite;
  -o-animation: rotation 3s linear infinite;
}
.placeholder-img.noMatch {
  background-image: url(assets/noMatch.svg);
  font-size: 0.5em;
}
.placeholder-img.noMatch.monochrome {
  background-image: url(assets/noMatchMono.svg);
}
.placeholder-img.noData {
  background-image: url(assets/noData.svg);
}
.placeholder-img.noData.monochrome {
  background-image: url(assets/noDataMono.svg);
}
.placeholder-img.noAccess {
  background-image: url(assets/noAccess.svg);
}
.placeholder-img.noAccess.monochrome {
  background-image: url(assets/noAccessMono.svg);
}
.placeholder-img.notFound {
  background-image: url(assets/notFound.svg);
}
.placeholder-img.notFound.monochrome {
  background-image: url(assets/notFoundMono.svg);
}
@-webkit-keyframes rotation {
from {
    -webkit-transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
}
}
.placeholder-tip {
  font-size: 0.7em;
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  color: rgba(0, 0, 0, 0.6);
}
.placeholder-tip span {
  margin: 0 0.2em;
}
.placeholder-load-fail {
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
      -ms-flex-direction: column;
          flex-direction: column;
}



