
.drop-body {
  position: absolute;
  visibility: hidden;
}
.drop-body.drop-down {
  top: 100%;
  left: 0;
}
.drop-body.drop-up {
  bottom: 100%;
  left: 0;
}
.drop-body.drop-left {
  right: 100%;
  top: 0;
}
.drop-body.drop-right {
  left: 100%;
  top: 0;
}
.drop-body.show-drop {
  visibility: visible;
}



