
.scroll-mask-container {
  position: relative;
  overflow: hidden;
}
.scroll-mask-container.vertical .scroll-content {
  overflow-x: hidden;
  overflow-y: auto;
}
.scroll-mask-container.vertical .scroll-content::-webkit-scrollbar {
  width: 0px;
}
.scroll-mask-container.vertical .scroll-content *:first-child {
  margin-top: 0.5em;
}
.scroll-mask-container.vertical .scroll-content *:last-child {
  margin-bottom: 0.5em;
}
.scroll-mask-container.vertical .mask-grad-top {
  top: 0;
  left: 0;
  right: 0px;
  height: 0.5em;
  background: -webkit-gradient(linear, left bottom, left top, from(transparent), color-stop(70%, #fff));
  background: -webkit-linear-gradient(bottom, transparent, #fff 70%);
  background: linear-gradient(to top, transparent, #fff 70%);
}
.scroll-mask-container.vertical .mask-grad-bottom {
  bottom: 0;
  left: 0;
  right: 0px;
  height: 0.5em;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(70%, #fff));
  background: -webkit-linear-gradient(top, transparent, #fff 70%);
  background: linear-gradient(to bottom, transparent, #fff 70%);
}
.scroll-mask-container.horizontal .scroll-content {
  overflow-x: auto;
  overflow-y: hidden;
}
.scroll-mask-container.horizontal .scroll-content::-webkit-scrollbar {
  width: 0px;
}
.scroll-mask-container.horizontal .scroll-content *:first-child {
  margin-left: 0.5em;
}
.scroll-mask-container.horizontal .scroll-content *:last-child {
  margin-right: 0.5em;
}
.scroll-mask-container.horizontal .mask-grad-top {
  top: 0;
  bottom: 0px;
  left: 0;
  width: 0.5em;
  background: -webkit-gradient(linear, right top, left top, from(transparent), color-stop(70%, #fff));
  background: -webkit-linear-gradient(right, transparent, #fff 70%);
  background: linear-gradient(to left, transparent, #fff 70%);
}
.scroll-mask-container.horizontal .mask-grad-bottom {
  top: 0;
  bottom: 0px;
  right: 0;
  width: 0.5em;
  background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(70%, #fff));
  background: -webkit-linear-gradient(left, transparent, #fff 70%);
  background: linear-gradient(to right, transparent, #fff 70%);
}
.mask-grad-top,
.mask-grad-bottom {
  position: absolute;
}
.scroll-content {
  background: #ffffff;
}



